import React, { useEffect } from 'react';
import { createPortal } from 'react-dom';

import { modalBackdropStyles, modalContainerStyles, modalWrapperStyles } from './styles';
import { useParent } from './utils/useParent';
import { usePortal } from './utils/usePortal';

export interface ModalProps {
  isVisible: boolean;
  children?: React.ReactElement | React.ReactNode | string;
  portalId?: string;
  parentId?: string;
  hasBackdrop?: boolean;
  customBackdrop?: React.ReactElement | React.ReactNode;
  customWrapperStyles?: React.CSSProperties;
  customBackdropStyles?: React.CSSProperties;
  customContainerStyles?: React.CSSProperties;
  onBackdropClick?: () => void;
  toggle?: () => void;
}

// Todo: add keyboard event listener to backdrop

export const Modal: React.FC<ModalProps> = ({
  isVisible,
  children,
  portalId = 'modal-root',
  parentId = '',
  hasBackdrop = true,
  customBackdrop,
  customWrapperStyles = modalWrapperStyles,
  customBackdropStyles = modalBackdropStyles,
  customContainerStyles = modalContainerStyles,
  onBackdropClick = () => null,
}) => {
  const parentElement = useParent(parentId);

  useEffect(() => {
    const modalRoot = usePortal(portalId);
    modalRoot?.appendChild(parentElement);

    return () => {
      modalRoot?.removeChild(parentElement);
    };
  }, [parentElement]);

  if (!isVisible) return null;

  return createPortal(
    <div style={customWrapperStyles}>
      {hasBackdrop
        ? customBackdrop || (
            <div style={customBackdropStyles} onClick={onBackdropClick} />
          )
        : null}
      <div style={customContainerStyles}>{children}</div>
    </div>,
    parentElement,
  );
};
